<template>
  <div class="status-container">
    <div id="title"><span class="font-green">Page:</span> {{getActualRoute}}</div>
    <div id="date">{{datenow}}</div>
    <div id="clock">{{timenow}}</div>
    <div><audio src="../assets/audio/plastic_love.mp3" controls></audio></div>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  data () {
    return {
      datenow: String,
      timenow: String
    }
  },

  computed: {
    getActualRoute () {
      return this.$store.state.actualRoute
    }
  },

  mounted () {
    return setInterval(() => {
      this.datenow = moment().format('dddd, MMMM Do YYYY')
      this.timenow = moment().format('h:mm/ss a')
    }, 1000)
  }
}
</script>

