.status-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
}

#clock {
  color: yellow;
}

audio {
  height: 20px;
}

@media only screen and (max-width: 670px) {
  .status-container {
    flex-direction: column;
    width: 100%;
    min-width: 300px;
    justify-content: center;
    text-align: center;
  }
}

</style>
