import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    actualRoute: '',
    bannerTitle: ''
  },
  mutations: {
    setActualRoute (state, payload) {
      state.actualRoute = payload
    },
    setBannerTitle (state, payload) {
      state.bannerTitle = payload
    }
  },
  actions: {
  },
  modules: {
  }
})
